import React from 'react';



const Cog = ({ cogToggleUnlock, cogToggleExplore, cogTogglePlan, cogToggleBuild, cogToggleLaunch }) => {
	return (
		<div className='mb5 pb5'>
			<div className = 'flex flex-row justify-center flex-wrap coglist'>
				<div onClick={cogToggleUnlock} id="unlock" className="cog grow pointer flex justify-center items-center fruition-yellow">
					<div className= "spoke fruition-yellow"></div>
					<div className="spoke fruition-yellow"></div>
					<div className="spoke fruition-yellow"></div>
					<div className="spoke fruition-yellow"></div>
					<div className="spoke fruition-yellow"></div>
					<div className="spoke fruition-yellow"></div>
					<div id ="example" className="step"><p className='mb2 white'>Unlock</p></div>
				</div>
				<div onClick={cogToggleExplore} id="explore" className="cog cogEx grow pointer flex justify-center items-center fruition-yellow">
					<div className= "spoke fruition-yellow"></div>
					<div className="spoke fruition-yellow"></div>
					<div className="spoke fruition-yellow"></div>
					<div className="spoke fruition-yellow"></div>
					<div className="spoke fruition-yellow"></div>
					<div className="spoke fruition-yellow"></div>
					<div className="step"><p className='mb2 white'>Explore</p></div>
				</div>
				<div onClick={cogTogglePlan} id="plan" className="cog cogEx grow pointer flex justify-center items-center fruition-yellow">
					<div className= "spoke fruition-yellow"></div>
					<div className="spoke fruition-yellow"></div>
					<div className="spoke fruition-yellow"></div>
					<div className="spoke fruition-yellow"></div>
					<div className="spoke fruition-yellow"></div>
					<div className="spoke fruition-yellow"></div>

					<div className="step"><p className='mb2 white'>Plan</p></div>
				</div>
				<div onClick={cogToggleBuild} id="build" className="cog cogEx grow pointer flex justify-center items-center fruition-yellow">
					<div className= "spoke fruition-yellow"></div>
					<div className="spoke fruition-yellow"></div>
					<div className="spoke fruition-yellow"></div>
					<div className="spoke fruition-yellow"></div>
					<div className="spoke fruition-yellow"></div>
					<div className="spoke fruition-yellow"></div>
					<div className="step"><p className='mb2 white'>Build</p></div>
				</div>
				<div onClick={cogToggleLaunch} id="launch" className="cog cogEx grow pointer flex justify-center items-center fruition-yellow">
					<div className= "spoke fruition-yellow"></div>
					<div className="spoke fruition-yellow"></div>
					<div className="spoke fruition-yellow"></div>
					<div className="spoke fruition-yellow"></div>
					<div className="spoke fruition-yellow"></div>
					<div className="spoke fruition-yellow"></div>
					<div className="step"><p className='mb2 white'>Launch</p></div>
				</div>	
			</div>
		</div>
	);
}

export default Cog
		